body {
    margin: 0;
    font: calc(.5em + 2vmin)/1.6 -apple-system, BlinkMacSystemFont,
        "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
    text-align: center;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

header, footer, main {
    width: 100%;
    display: inherit;
    flex-direction: inherit;
    align-items: inherit;
    justify-content: inherit;
}

header, footer {
    background-color: #282c34;
    color: white;
}

main {
    flex: 1 1;
    justify-content: flex-start;
}

header {
    min-height: 20vh;
    font-size: 1.4em;
}

footer {
    min-height: 5vh;
}

.App-link {
    color: #61dafb;
}

.show-color {
    /* display: block; */
}

.color-title {
    /* font: 20px/120%; */
}

.color-sample {
    width: 5em;
    height: 3em;
    margin: .2em auto;
    border-radius: 50% 50%;
}

.StarRating {
    display: flex;
    flex-direction: row;
    margin: 0.2em 0.5em;
    /* cursor: pointer; */
}

.star {
    width: 1em;
    height: 1em;
    cursor: pointer;
}

.star::before {
    content: '';
    display: block;
    position: relative;
    width: 1em;
    height: 1em;
    margin-left: -50%;
    left: 50%;
    background: silver;
    -webkit-clip-path: polygon(50% 0, 65% 35%, 100% 40%, 70% 60%, 80% 100%,
                        50% 70%, 20% 100%, 30% 60%, 0 40%, 35% 35%);
            clip-path: polygon(50% 0, 65% 35%, 100% 40%, 70% 60%, 80% 100%,
                        50% 70%, 20% 100%, 30% 60%, 0 40%, 35% 35%);
}

.selected::before {
    background-color: gold;
}

.highlighted::before {
    background-color: orange;
}

ul {
    list-style-type: none;
    display: flex;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;
    width: 80%;
    margin: 0;
    padding: 0;
}

li {
    margin: 0.2em 1em;
    display: inherit;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

form {
    min-width: 50%;
}

fieldset {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 1em auto;
    border: 1px dashed hsla(0, 0%, 0%, 0.4);
    min-width: 20em;
    min-height: 3em;
}

input {
    display: inline-block;
    padding: 0 .4em;
    margin: .25em .2em 0 0;
    font: inherit;
    vertical-align: middle;
    height: 1.5em;
}

input[type="text"] {
    width: 5em;
}

input[type="color"] {
    width: 3em;
}

